import Sprite from '../enums/Sprite.js';

export default class Character extends Entity {
    constructor() {
        super();

        this.sprite = Sprite.PLANE;
        this.shooting = false;
        this.cooldown = 0;
    }

    shoot() {
        if (this.cooldown == 0) {
            this.shooting = true;
            this.cooldown = 1;
        }
    }

    update() {
        super.update();
        if (this.cooldown > 0) {
            this.cooldown -= 0.02;
        } else {
            this.cooldown = 0;
        }
    }
}